body {
  margin: 0;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /*overflow-y: scroll;*/
  /*scrollbar-gutter: auto;*/

//background-color: bisque;
  background-color: var(--global-primary-color)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*Обнуление*/
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul, ol, li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,h2,h3,h4,h5,h6 {
  font-size:inherit;
  font-weight:inherit;
}

input, button {
  border: none;
  font-family:inherit;
  font-size:inherit;
}

html, body {
  height: 100%;
  line-height: 1;
}

:root {
  --global-primary-color: #e1d4c9;
  --global-secondary-color: #324A5E;
  --global-third-color: #6e6b6b;
  /*--global-fourth-color: #665F55;*/
}

